    background-position: 0 0;
  }
}

@keyframes edit-show {
  0% {
    overflow-y: hidden;
    visibility: visible;
    min-height: 100vh;
    max-height: 100vh;
    opacity: 0;
    padding-top: 5vh;
  }

  99% {
    overflow-y: hidden;
  }

  100% {
    overflow-y: auto;
    opacity: 1;
    padding-top: 0;
  }
}

@keyframes edit-close {
  0% {
    visibility: visible;
    overflow-y: hidden;
    min-height: 100vh;
    max-height: 100vh;
    opacity: 1;
    padding-top: 0;
  }

  99% {
    visibility: visible;
    min-height: 100vh;
    max-height: 100vh;
  }

  100% {
    overflow-y: hidden;
    visibility: hidden;
    min-height: 0;
    max-height: 0;
    opacity: 0;
    padding-top: 5vh;
  }
}
